
body{
    margin: 0;
    padding: 0;
    background-color:rgba(252, 248, 248, 0.897);
    font-family: 'Poppins';
}

.header{
    background-color: rgb(219, 58, 30);
    padding: 5px 8%;
    
}

#logo{
    background-color: white;
    color: red;
    font-size: 25px;
    height: 35px;
    width: 35px;
    text-align: center;
    border-radius: 50%;
    float: left;
    
}

.menu{
    padding-top: 5px;
    text-align: right;
    float: right;
    font-size: 14px;
}
.login{
    
    text-decoration: none;
    color: white;
}
.login:hover{
    
    text-decoration: none;
    color: white;
}


#account{
    background-color:  rgb(219, 58, 30);
    color: white;
    border:1px solid white;
    padding: 6px;
    font-size: 12px;
    margin-right: 20px;
}
#heading{
    font-weight: bold;
    color: #192f60;
    font-weight: bolder;
}
#he4{
    padding-top: 0;

}

.p{
    color: rgb(72, 45, 128);  
    font-weight: bold;
    font-size: 15px;
}
span{
    color: #8c96ab;
    font-size: 15px;
}



#main{
    margin: auto 120px;
    
}





#select{
    width: 100%;
    height: 30px;
    color: #8c96ab;
    font-weight: bold;
}



.cards:hover{
    transform: scale(1.05, 1.05);
    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.5);
}


.floatLeft{
    float: left;
    padding-left: 30px;
}





.Path-6195 {
    width: 90%;
    height: 0;
    margin: 0 auto;
    border: solid 1px #dedfe5;
    margin-top: 15px;
  }


#pageid{
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    text-align: center;
    margin-bottom: 20px;
}

.button{
    background-color: transparent;
    padding: 5px;
    width: 35px;
    font-weight: bold;
    font-family: 'Poppins';
    border: 2px solid grey;
    color: rgb(114, 103, 103);
    border-radius: 10%;
}
.button:hover{
    background-color: rgb(170, 103, 136);
    color: white;
    
}

.activatebtn{
    background-color: rgb(110, 107, 107);
    color: honeydew;
}


#first{
    width: 80%;
    float: left;
    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
    padding:0 15px;
    padding-bottom: 26px;
    background-color: white;
    
    margin-left: auto;
    margin-right: auto;
}


#second{
    width: 85%;
    float: left;    
    margin-left: auto;
    margin-right: auto;

}

#cardContainer{
    margin-top: 40px;
}

.cards{
    padding: 25px 0;
    background-color: white;
    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 45px;
}


.img{


    height: 200px;
    width: 90%;
    border-radius: 20%;
}

@media screen and (min-width: 900px) {
    #first{
        width: 20%;
        float: left;
        box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
        padding:0 15px;
        padding-bottom: 26px;
        background-color: white;
    }
    
    #cardContainer{
        margin-top: 0;
    }

    #second{
        width: 60%;
        float: left;    
    }


    .cards{
        padding: 25px 0;
        background-color: white;
        box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
        margin-left: 45px;
        margin-bottom: 30px;
    }

    .img{
        
        height: 300px;
        width: 100%;
        border-radius: 20%;

        
    }
  }




 
  @media screen and (min-width: 1200px) {
    #first{
        width: 20%;
        float: left;
        box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
        padding:0 15px;
        padding-bottom: 26px;
        background-color: white;
    }

    #cardContainer{
        margin-top: 0;
    }
    
    
    #second{
        width: 60%;
        float: left;    
        
    }

    .cards{
        padding: 25px 0;
        background-color: white;
        box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
        margin-left: 45px;
        margin-bottom: 30px;
    }

    .img{
        height: 90px;
        width: 90px;
        border-radius: 20%;
    }
  }



  

